<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack"></span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>{{title}}</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" ref="TableDataRef" :model="params" class="demo-form-inline">
        <el-form-item label="" size="mini" prop="company_id" :rules="rules.company_id">
          {{(function(){if(params.company_id == 0)params.company_id = null})()}}
          <el-select v-model="params.company_id" placeholder="" filterable @change="companyChange" @focus="companyListInvok" :disabled="isDisabled" :loading="loading">
            <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
              <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-input v-model="params.stock_taking_sn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" size="mini" prop="business_time" :rules="rules.business_time">
          <el-date-picker v-model="params.business_time" type="date" placeholder="" value-format="yyyy-MM-dd">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="" size="mini" prop="warehouse_id" :rules="rules.warehouse_id">
          {{(function(){if(params.warehouse_id == 0)params.warehouse_id = null})()}}
          <el-select clearable v-model="params.warehouse_id" filterable placeholder="" @focus="warehouseInvok" @change="warehouseChange" :disabled="isDisabled">
            <el-option :label="item.warehouse_name" :value="item.warehouse_id" :key="item.warehouse_id" v-for="item in warehouseList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          {{(function(){if(params.allocation_id == 0)params.allocation_id = null})()}}
          <el-select clearable v-model="params.allocation_id" filterable placeholder="" @focus="InventoryInvok" :disabled="isDisabled">
            <el-option :label="item.allocation_name" :value="item.allocation_id" :key="item.allocation_id" v-for="item in allocationInventoryList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-input v-model="params.instorage_sn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-input v-model="params.outstock_sn" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" size="mini">
          {{(function(){if(params.keeper_id == 0)params.keeper_id = null})()}}
          <el-select clearable v-model="params.keeper_id" placeholder="" filterable @focus="EmployeeListInvok" :disabled="isDisabled">
            <el-option :label="item.employee_name" :value="item.employee_id" :key="item.employee_id" v-for="item in employeeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini">
          <getBrand v-model="params.brand_id" :fatherParams='params'  :disabled="isDisabled" />
        </el-form-item>
        <el-form-item label="" size="mini">
          {{(function(){if(params.host_goods_id == 0)params.host_goods_id = null})()}}
          <v-GoodsList :scopes='{row: params}' :keyObj="{ value: 'host_goods_id', label: 'host_goods_name' }" :vxeParams='{company_id: params.company_id,is_incomplete:0}' :changeGoods='handleSelect' :showObj="{goods_id: '' , goods_name: ''}" />
        </el-form-item>

        <el-form-item label="" size="mini">
          <el-input v-model="params.remark" maxlength="100" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" size="mini">
          <el-select clearable v-model="params.auditor_status" placeholder="" :disabled="true">
            <el-option label="" :value="0"></el-option>
            <el-option label="" :value="1"></el-option>
            <el-option label="" :value="2"></el-option>
            <el-option label="" :value="3"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" size="mini" v-if="isDisabled">
          {{(function(){if(params.goods_type_id == 0)params.goods_type_id = null})()}}
          <jybd-cascader ref="cascader" @cascaderChange="handleChange"></jybd-cascader>
        </el-form-item>
        <el-form-item label="" v-else>
          <jybd-cascader ref="cascader" @cascaderChange="handleChange"></jybd-cascader>
        </el-form-item>
      </el-form>
    </div>
    <div class="add_delet">
      <el-radio-group v-model="params.scope_type" :disabled="isDisabled">
        <el-radio :label="0"></el-radio>
        <el-radio :label="1">&gt;0</el-radio>
        <el-radio :label="2">&lt;0</el-radio>
        <el-radio :label="3">0</el-radio>
      </el-radio-group>
    </div>
    <br />
    <div class="add_delet">
      <el-button icon="el-icon-circle-plus-outline" type="primary" @click="addF" size="mini"></el-button>
      <el-button plain icon="el-icon-delete" @click="deleteF" size="mini"></el-button>
      <el-button @click="summaryF" size="mini" type="primary" v-if="authority.b_extract==1"></el-button>
      <el-button @click="panF" size="mini" type="primary" v-if="isShow"></el-button>
      <el-button @click="resetF" size="mini" plain></el-button>
      <el-button @click="printF" size="mini" type="primary"></el-button>
      <el-button plain icon="iconfont icon-daoru5" @click="importExcel" v-if="authority.b_import==1" size="mini"></el-button>
      <el-button @click="downLoadFile(false)" plain icon="el-icon-download" v-if="authority.b_export_file==1" size="mini"></el-button>
      <el-button plain icon="iconfont icon-daochu10" @click="exportExcel" size="mini"></el-button>
    </div>

    <div class="table_container">
      <xTable ref='xTable' :isEdit='activeMethod' :edit-rules='editRules' :tableColumn='tableColumn' :showFooter='true' :footerMethod='footerMethod' />
    </div>
    <div slot="footer" class="dialog-footer">
      <el-button @click="goBack" size="mini" plain> </el-button>
      <el-button @click="submit('TableDataRef')" size="mini" type="primary"> </el-button>
    </div>
    <!--  -->
    <div v-if="a">
      <takestockPrint ref="printRef"></takestockPrint>
    </div>
    <upload :options="options" @uploadFail="uploadFail" ref="upload" :noTips='true'></upload>
    <excelExport ref="excelExport" v-show="false" :headerTitle='headerTitle' :titles="titles" :dataList='dataList'></excelExport>

  </div>
</template>


<script>
import { mapActions, mapGetters } from "vuex";
import { opthionList, repertory } from "common/utils/public_function/commonUtils";
import { showFullScreenLoading } from "@/utils/loading";
import takestockPrint from "common/components/public/takestockPrint.vue"; //
import {
  tableHead,
  Colmodule,
  exportColumn,
} from "./tableHead/takestockAddHead";
import { exportTable } from "@/utils/exportExcel";
export default {
  name: "instorageTakestockTakestockAdd",
  mixins: [tableHead],
  data() {
    return {
      a: true,
      options: {
        title: "",
        url: "/v2/inventory/StockTaking/importStockTakingGoods",
        params: null,
      },
      isShow: false,
      dataList: [],
      headerTitle: [],
      titles: "",
      loading: false,
      isDisabled: false, // 
      company_Name: localStorage.getItem("company_name"),
      warehouse_Name: "",
      keeper_Name: "",
      title: "",
      realGoodsList: [], //
      TableData: {
        goodsList: [],
      },
      params: {},
      selectedOptions: [],

      props: {
        value: "value",
        lable: "label",
        children: "child",
      },
    };
  },
  computed: {
    ...mapGetters(["takestockAddGet"]),
  },
  components: {
    takestockPrint,
  },
  methods: {
    ...mapActions(["takestockIndexACT"]),
    // 
    mergeData() {
      setTimeout(() => {
        this.$refs.xTable._proxy("updateFooter");
        if (this.$refs.xTable._proxy("getTableData").fullData.length) {
          this.isShow = true;
        } else {
          this.isShow = false;
        }
      }, 500);
    },
    // 
    uploadFail(result) {
      if (result.code == 400) {
        if (Object.keys(result.data).length || result.data.length) {
          this.$refs.upload.close();
          this.$confirm("", "", {
            confirmButtonText: "",
            customClass: "InventoryClass",
            message: `:${
              result.data.success_count + result.data.failure_count
            };     :${
              result.data.success_count
            },       :${result.data.failure_count}`,
            closeOnClickModal: false,
            type: "warning",
          })
            .then(() => {
              this.headerTitle = result.data.title;
              this.dataList = result.data.error_data;
              this.titles = result.data.file_name;
              this.$refs.excelExport.exportClick();
            })
            .catch(() => {});
        }
      } else {
        // // 
        var box = this.$refs.xTable
            ._proxy("getTableData")
            .fullData.concat(result.data.success_data),
          noRepeat = {}, // 
          index = 0, // 
          keyBox = {}; // key
        result = box.filter((val, key, arr) => {
          //       
          arr[key].real_stocks = val.real_stocks
            ? val.real_stocks
            : val.new_stocks;
          arr[key].stocks_num = val.stocks_num
            ? val.stocks_num
            : val.difference_num;
          if (
            noRepeat[val.goods_id] &&
            noRepeat[val.goods_id][val.batch] &&
            noRepeat[val.goods_id][val.batch][val.allocation_id] !== undefined
          ) {
            index++;
            keyBox[noRepeat[val.goods_id][val.batch][val.allocation_id]] = val;
            return false;
          } else {
            if (!val.goods_id) {
              return false;
            }
            if (noRepeat[val.goods_id] === undefined && val.goods_id) {
              // 
              noRepeat[val.goods_id] = {};
            }
            if (
              noRepeat[val.goods_id] &&
              noRepeat[val.goods_id][val.batch] === undefined &&
              val.batch
            ) {
              // 
              noRepeat[val.goods_id][val.batch] = {};
            }
            if (
              noRepeat[val.goods_id] &&
              noRepeat[val.goods_id][val.batch] &&
              noRepeat[val.goods_id][val.batch][val.allocation_id] ===
                undefined &&
              val.allocation_id
            ) {
              // 
              noRepeat[val.goods_id][val.batch][val.allocation_id] = key;
            }
            return true;
          }
        });
        result = result.map((val, key) => {
          for (var i in keyBox) {
            if (key === i - 0) {
              return keyBox[i] ? keyBox[i] : val;
            }
          }
          return val;
        });
        if (index > 0) {
          this.$message.success("" + index + "");
        }
        this.$refs.xTable._proxy("loadData", result);
        this.mergeData();
      }
    },
    // 
    importExcel() {
      if (!this.params.company_id || !this.params.warehouse_id) {
        this.$message.warning("");
        return;
      }
      this.options.params = {
        company_id: this.params.company_id,
        warehouse_id: this.params.warehouse_id,
      };
      this.$nextTick(() => {
        this.$refs.upload.stateOpen();
      });
    },
    // 
    downLoadFile() {
      //  v2/inventory/StockTaking/downloadFileUrl true
      this.$axMethod.downloadFileUrlStockTaking().then((response) => {
        if (response.status == 200) {
          window.location.href = response.data.code.url;
        }
      });
    },
    // Excel
    exportExcel() {
      var column = exportColumn.concat([]);
      this.warehouseChange(true);
      //  
      var top = "" + this.warehouse_Name,
        bottom =
          "" +
          localStorage.getItem("realname") +
          "                                              " +
          this.$utils.toDateString(new Date(), "yyyy-MM-dd HH:mm:ss");
      var total = this.$refs.xTable
        ._proxy("getTableData")
        .footerData[0].concat([]);
      total.splice(0, 1);
      total.splice(3, 1, "");
      exportTable(
        this.$refs.xTable._proxy("getTableData").fullData,
        column,
        "",
        top,
        total,
        bottom
      );
    },
    // 
    footerMethod({ columns, data }) {
      return [
        columns.map((column, columnIndex) => {
          if (columnIndex === 1) {
            return "";
          }
          if (
            ["stocks", "real_stocks", "stocks_num"].includes(column.property)
          ) {
            if (this.$refs.xTable) {
              return this.$utils.sum(
                this.$refs.xTable._proxy("getTableData").fullData,
                column.property
              );
            } else {
              return null;
            }
          }
          return null;
        }),
      ];
    },
    handleChange(val) {
      this.params.goods_type_id = val[2];
    },
    handleItemChange(val = []) {
      this.$axMethod.getselectType({ p_id: "" }).then((res) => {
        if (res.data.code == 200) {
          this.cascaderData = res.data.data.map((item) => {
            return {
              value: item.goods_type_id,
              label: item.goods_type_name,
              child: [],
            };
          });
          if (val[0]) {
            var index = 0;
            this.$axMethod.getselectType({ p_id: val[0] }).then((res) => {
              if (res.data.code == 200) {
                this.cascaderData.forEach((item, i) => {
                  if (item.value == val[0]) {
                    index = i;
                    item.child = res.data.data.map((item) => {
                      return {
                        value: item.goods_type_id,
                        label: item.goods_type_name,
                        child: [],
                      };
                    });
                  }
                });

                if (val[1]) {
                  this.$axMethod.getselectType({ p_id: val[1] }).then((res) => {
                    if (res.data.code == 200) {
                      this.cascaderData[index].child.forEach((item, i) => {
                        if (item.value == val[1]) {
                          item.child = res.data.data.map((item) => {
                            return {
                              value: item.goods_type_id,
                              label: item.goods_type_name,
                            };
                          });
                        }
                      });
                    }
                  });
                }
              }
            });
          }
        }
      });
    },
    // 
    handleSelect(value, { row }) {
      row.goods_id = value.goods_id;
      row.unit_name = value.unit_name;
      row.purchase_price = value.purchase_price;
      let arr = [
        "production_time",
        "batch",
        "remaining_goods_num",
        "subtotal_price",
        "allocation_id",
      ];
      arr.forEach((item) => {
        let val = null;
        if (item === "subtotal_price") val = 0;
        row[item] = val;
      });
      var list = this.$refs.xTable._proxy("getTableData").fullData;
      list[list.length - 1].goods_id ? this.addF(true) : false;
    },
    goBack() {
      this.$router.push({
        path: "/inventory/takestock",
      });
      this.takestockIndexACT({
        goPage: this.takestockAddGet.goPage,
        params: this.takestockAddGet.params,
        pageSize: this.takestockAddGet.pageSize,
      });
    },
    getGoodsIds(arr) {
      return arr.map((obj) => obj.goods_id);
    },
    companyChange(row) {
      let arr = ["warehouse_id", "allocation_id", "keeper_id"];
      arr.forEach((item) => {
        this.$set(this.params, item, null);
      });
      this.$refs.xTable._proxy("loadData", []);
    },
    //    step = true 
    warehouseChange(step) {
      if (step !== true) {
        this.$set(this.params, "allocation_id", null);
      }
      this.warehouseList.forEach((val) => {
        if (val.warehouse_id === this.params.warehouse_id) {
          this.warehouse_Name = val.warehouse_name;
        }
      });
    },
    allocationChange({ row }, obj) {
      this.$set(row, "stocks", obj.stocks);
      row.allocation_name = obj.allocation_name;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // / select 
    createStateFilter(queryString) {
      return (state) => {
        return (
          state.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
        );
      };
    },
    addF(step) {
      if (this.params.warehouse_id || step) {
        if (this.$refs.xTable) {
          this.$refs.xTable._proxy("insertAt", Colmodule, -1);
        }
        this.mergeData();
      } else {
        this.$message.warning("");
      }
    },
    printF() {
      this.a = false;
      this.a = true;
      this.$nextTick(() => {
        this.$refs.printRef.printPdf({
          goodsList: this.$refs.xTable
            ._proxy("getTableData")
            .fullData.concat([]),
          instoragList: this.params,
        });
      });
    },
    // 
    getShopList() {
      this.isDisabled = true;
      this.$axMethod
        .stockTakingGetGoodsList({
          ...this.params,
          type: 1,
        })
        .then((res) => {
          res.data.data = res.data.data.map((obj) => {
            if (!this.getGoodsIds(this.realGoodsList).includes(obj.goods_id)) {
              this.realGoodsList.push({
                goods_id: obj.goods_id,
                goods_name: obj.goods_name,
              });
            }
            return {
              ...obj,
              allocationList: [
                {
                  allocation_name: obj.allocation_name,
                  allocation_id: obj.allocation_id,
                },
              ],
              is_extract: 1,
            };
          });
          //
          let tableData = [];
          tableData.push(...res.data.data);
          this.$refs.xTable._proxy("loadData", tableData);
          this.mergeData();
          if (res.data.data.length) {
            this.$message.success("");
          } else {
            this.$message.warning("");
          }
        });
    },
    // 
    summaryF() {
      if (this.params.warehouse_id && this.params.company_id) {
        if (
          this.isDisabled &&
          this.$refs.xTable._proxy("getTableData").fullData.length > 0
        ) {
          this.$confirm(", ?", "", {
            confirmButtonText: "",
            cancelButtonText: "",
            type: "warning",
          })
            .then(() => {
              this.getShopList();
            })
            .catch(() => {});
        } else {
          this.getShopList();
        }
      } else {
        this.$message.warning("");
      }
    },
    // 
    panF() {
      var list = this.$refs.xTable._proxy("getTableData").fullData;
      list.map((val) => {
        val.real_stocks = val.stocks;
        val.stocks_num = val.real_stocks - val.stocks;
      });
      this.$refs.xTable._proxy("loadData", list);
      this.$message.success("");
      this.mergeData();
    },
    // 
    resetF() {
      this.isDisabled = false;
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date()),
        scope_type: 0,
      });
      this.$set(this.TableData, "instoragList", {});
      this.$set(this.TableData, "instoragList", {
        company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date()),
        scope_type: 0,
      });
      this.params.company_id = Number(localStorage.company_id);
      this.$refs.xTable._proxy("loadData", []);
      this.mergeData();
      opthionList(this, window.localStorage);
    },
    // 
    deleteF() {
      var list = this.$refs.xTable._proxy("getSelectRecords");
      if (list.length) {
        this.$refs.xTable._proxy("removeSelecteds");
      } else {
        this.$message.warning("");
      }
      this.mergeData();
    },
    // 
    insert(row) {
      this.$refs.xTable._proxy("insertAt", {}, row);
      this.mergeData();
    },
    // 
    copy(row) {
      this.$refs.xTable._proxy("insertAt", row, row);
      this.mergeData();
    },
    submit(formName) {
      let flag = true;
      let vm = this;
      var list = this.$refs.xTable._proxy("getTableData").fullData;
      if (!list.length) {
        this.$message.warning("");
        return false;
      }
      let tableLen = list.length;
      let hasLastGoodsName = !!list[tableLen - 1].goods_name ? true : false; //
      if (!hasLastGoodsName) {
        list.pop();
      }
      this.$refs[formName].validate((valid) => {
        if (valid) {

          this.$refs.xTable._proxy("validate").then(() => {
            let params = {};
            params.goods_info = list.map((item) => {
              return {
                stock_taking_goods_id: item.stock_taking_goods_id
                  ? item.stock_taking_goods_id
                  : 0,
                goods_id: item.goods_id,
                batch: item.batch,
                allocation_id: item.allocation_id,
                stocks: item.stocks,
                real_stocks:
                  item.real_stocks || item.real_stocks === 0
                    ? item.real_stocks
                    : "",
                stocks_num:
                  item.stocks_num || item.stocks_num === 0
                    ? item.stocks_num
                    : "",
                remark: item.remark ? item.remark : "",
                is_entity: item.is_entity,
                is_extract: item.is_extract ? item.is_extract : 0, // is_extract  1   0
              };
            });
            params.goods_info = JSON.stringify(params.goods_info);
            params = Object.assign({}, params, this.params);
            params.remark = params.remark ? params.remark : "";
            let api = !this.takestockAddGet.stock_taking_id
              ? "addTakeStock"
              : "editTakeStock";
            if (this.takestockAddGet.stock_taking_id) {
              params.stock_taking_id = this.takestockAddGet.stock_taking_id;
            }
            let reperinfo = params.goods_info;
            reperinfo = JSON.parse(reperinfo);
            reperinfo.forEach((item) => {
              item.warehouse_id = this.params.warehouse_id;
            });
            try {
              var loadingBox =
                document.getElementsByClassName("loading-area")[0];
              loadingBox.style.position = "relative";
            } catch (error) {
              var loadingBox;
            }
            showFullScreenLoading();
            repertory(
              this,
              {
                company_id: this.params.company_id,
                goods_info: JSON.stringify(reperinfo),
                status: 1,
                type: 6,
              },
              function () {
                vm.$axMethod[api](params)
                  .then(({ data }) => {
                    if (data.code == "200") {
                      vm.$message.success(data.data);
                      vm.goBack();
                    }
                    loadingBox.style.position = "static";
                  })
                  .catch(() => {
                    loadingBox.style.position = "static";
                  });
              },
              () => {
                loadingBox.style.position = "static";
              }
            );
          });
        }
      });
    },
    // 
    toPositiveNum(row, name) {
      if (isNaN(row[name])) {
        row[name] = "";
      }
      if (row[name] >= 1e8 || row[name] < 0) {
        this.$message.warning("0!");
        row[name] = "";
      }
      row.stocks_num = row[name] - row.stocks;
      this.$refs.xTable._proxy("reloadRow", row);
      this.mergeData();
    },
  },
  mounted() {
    this.title = this.takestockAddGet.stock_taking_id ? "" : "";
    if (this.takestockAddGet.stock_taking_id) {
      this.params.stock_taking_id = this.takestockAddGet.stock_taking_id;
      this.$axMethod.infoTakeStock(this.params).then(({ data }) => {
        if (data.code == "200") {
          let flag = true;
          // this.$set(this.TableData, "instoragList", data.data.instoragList);
          this.$set(this, "params", data.data.instoragList);
          this.realGoodsList.push({
            goods_id: this.params.host_goods_id,
            goods_name: this.params.host_goods_name,
          });
          this.selectedOptions = [
            data.data.instoragList.goods_type_id_1,
            data.data.instoragList.goods_type_id_2,
            data.data.instoragList.goods_type_id,
          ];
          data.data.goodsList = data.data.goodsList.map((obj) => {
            // this.getGoodsIds();
            if (!this.realGoodsList.includes(obj.goods_id)) {
              this.realGoodsList.push({
                goods_id: obj.goods_id,
                goods_name: obj.goods_name,
              });
            }
            if (flag && obj.is_extract == 1) {
              this.isDisabled = true;
              flag = false;
            }
            return {
              ...obj,
              allocationList: [
                {
                  allocation_name: obj.allocation_name,
                  allocation_id: obj.allocation_id,
                },
              ],
            };
          });
          this.handleItemChange(this.selectedOptions);
          this.$refs.xTable._proxy("loadData", data.data.goodsList);
          Object.assign(data.data.instoragList, {
            employee_id: data.data.instoragList.keeper_id,
            employee_name: data.data.instoragList.keeper_name,
          });
          opthionList(this, data.data.instoragList);
          this.$set(this.params, "scope_type", 0);
        }
      });
    } else {
      this.$set(this, "params", {
        company_id: Number(localStorage.company_id),
        business_time: this.$publicFution.formatData(new Date()),
        scope_type: 0,
      });
      opthionList(this, window.localStorage);
    }
    this.addF(true);
  },
};
</script>
