import xlsx from "xlsx";
import axios from "common/utils/axios";
// 
export function importExcel(file, callback, type = 'json') {
  var reader = new FileReader(file);
  reader.onload = function (e) {
    var data = e.target.result;
    var workbook = xlsx.read(data, {
      type: "binary"
    });
    var key = Object.keys(workbook.Sheets)
    if (type === 'html') {
      callback(xlsx.utils.sheet_to_html(workbook.Sheets[key[0]]))
    } else if (type === 'json') {
      callback(xlsx.utils.sheet_to_json(workbook.Sheets[key[0]]))
    }
  };
  reader.readAsBinaryString(file);
}
/**
 *
 * @param {*} arr = [  
 *                   ["", null, null, ""], // 2null
                     ["", "", "", ""]
                    ]
 * @param {*} merges = [    s e r c
                      {
                        s: {
                          r: 0,
                          c: 0
                        },
                        e: {
                          r: 0,
                          c: 2
                        }
                      },
                    ]
 */
export function exportExcal(arr, merges = [], name) {
  var sheet = xlsx.utils.aoa_to_sheet(arr);
  sheet["!merges"] = merges
  download(sheet2blob(sheet), name ? name + '.xlsx' : 'Excal.xlsx');
}
/**
 *
 * @param {Array} data 
 * @param {Array} column 
 * @param {String} name 
 * @param {String} top 
 * @param {String} total 
 * @param {String} bottom 
 * @param {Array} merges 
 */
export function exportTable(data, column, name, top, total, bottom, merges) {
  var exportColumn = column.concat([]), // 
    result = [], //
    nameBox = [], //
    exportTop = new Array(exportColumn.length - 1), // 
    exportMerges = [], // 
    exportBottom = new Array(exportColumn.length - 1); // 
  // 
  if (top) {
    exportTop.unshift(top)
    exportMerges.push({
      s: {
        r: 0,
        c: 0
      },
      e: {
        r: 0,
        c: exportColumn.length - 1
      }
    })
    result.push(exportTop)
  }
  // 
  exportColumn.forEach(twoVal => {
    nameBox.push(twoVal.title);
  });
  result.push(nameBox)
  // 
  data.forEach((val, key) => {
    var arr = [];
    exportColumn.forEach((twoVal, twoKey) => {
      if (twoVal.type === 'seq') {
        arr.push(key + 1);
      } else {
        arr.push(val[twoVal.field]);
      }
    });
    result.push(arr)
  })
  // 
  if (total) {
    result.push(total)
  }
  // 
  if (bottom) {
    exportBottom.unshift(bottom)
    result.push(exportBottom)
    exportMerges.push({
      s: {
        r: result.length - 1,
        c: 0
      },
      e: {
        r: result.length - 1,
        c: exportColumn.length - 1
      }
    })
  }
  if (merges) {
    exportMerges = exportMerges.concat(merges)
  }
  var sheet = xlsx.utils.aoa_to_sheet(result);
  sheet["!merges"] = exportMerges
  download(sheet2blob(sheet), name ? name + '.xlsx' : 'Excal.xlsx');
}
// 

export function downloadIamge(imgsrc, name) {//
  let image = new Image();
  //  Canvas 
  image.setAttribute("crossOrigin", "anonymous");
  image.onload = function () {
    let canvas = document.createElement("canvas");
    canvas.width = image.width;
    canvas.height = image.height;
    let context = canvas.getContext("2d");
    context.drawImage(image, 0, 0, image.width, image.height);
    let url = canvas.toDataURL("image/png"); //base64
    let a = document.createElement("a"); // a
    let event = new MouseEvent("click"); // 
    a.download = name || "photo"; // 
    a.href = url; // URLa.href
    a.dispatchEvent(event); // a
  };
  image.src = imgsrc;
}


export function downloadCanvasIamge(canvas, name) {//
  let url = canvas.toDataURL("image/png"); //base64
  let a = document.createElement("a"); // a
  let event = new MouseEvent("click"); // 
  a.download = name || "photo"; // 
  a.href = url; // URLa.href
  a.dispatchEvent(event); // a
}

