export function download(url, saveName) {
  if (typeof url == "object" && url instanceof Blob) {
    url = URL.createObjectURL(url); // blob
  }
  var aLink = document.createElement("a");
  aLink.href = url;
  aLink.target = '_brank';
  aLink.download = saveName || ""; // HTML5file:///
  var event;
  if (window.MouseEvent) event = new MouseEvent("click");
  else {
    event = document.createEvent("MouseEvents");
    event.initMouseEvent(
      "click",
      true,
      false,
      window,
      0,
      0,
      0,
      0,
      0,
      false,
      false,
      false,
      false,
      0,
      null
    );
  }
  aLink.dispatchEvent(event);
}
// sheetexcelblobURL.createObjectURL
function sheet2blob(sheet, sheetName) {
  sheetName = sheetName || "sheet1";
  var workbook = {
    SheetNames: [sheetName],
    Sheets: {}
  };
  workbook.Sheets[sheetName] = sheet;
  // excel
  var wopts = {
    bookType: "xlsx", // 
    bookSST: false, // Shared String TableIOS
    type: "binary"
  };
  var wbout = xlsx.write(workbook, wopts);
  var blob = new Blob([s2ab(wbout)], {
    type: "application/octet-stream"
  });
  // ArrayBuffer
  function s2ab(s) {
    var buf = new ArrayBuffer(s.length);
    var view = new Uint8Array(buf);
    for (var i = 0; i != s.length; ++i) view[i] = s.charCodeAt(i) & 0xff;
    return buf;
  }
  return blob;
}

export function loadImage(src) {
  return new Promise(function (req) {
    var myImage = new Image();
    myImage.crossOrigin = '';
    myImage.src = src; //
    myImage.onload = function () {
      req(myImage)
    }
  })
}

export async function drawImg(dataList, obj, callBack) {
  var { dom, width = 500 } = obj
  var canvas = document.createElement("canvas");
  canvas.width = width
  canvas.height = 0
  var imgBox = []
  let context = canvas.getContext("2d");
  for (let i = 0; i < dataList.length; i++) {
    const image = await loadImage(dataList[i])
    var imgHieght = Math.round(image.height / image.width * width)
    imgBox.push({
      dom: image,
      height: imgHieght,
      y: canvas.height
    })
    canvas.height += imgHieght
  }
  imgBox.forEach((val) => {
    context.drawImage(val.dom, 0, val.y, width, val.height)
  })
  if (dom) {
    dom.appendChild(canvas)
  }
  callBack && callBack(canvas)
}
