function loadFinish() {

}
export function setLoadFinish(func) {
  loadFinish = Debounce(function(){
    window.CLodopLoadFinComplete = true
    func()
  }, 500)
}
//==CLodopJS,800018000():==
function loadCLodop() {
  if (CLodopJsState == "loading" || CLodopJsState == "complete") return;
  CLodopJsState = "loading";
  var head = document.head || document.getElementsByTagName("head")[0] || document.documentElement;
  var JS1 = document.createElement("script");
  var JS2 = document.createElement("script");
  var srcold1 = "https://localhost:8443/CLodopfuncs.js?priority=1";
  var srcold2 = "https://localhost:8444/CLodopfuncs.js?priority=0";
  var src1 = "https://localhost.lodop.net:8443/CLodopfuncs.js?priority=1";
  var src2 = "https://localhost.lodop.net:8444/CLodopfuncs.js?priority=0";

  try {
    let version = verChrome[0].match(/\d+/)[0];
    if (version >= 84) {
      JS1.src = src1;
      JS2.src = src2;
    } else {
      JS1.src = srcold1;
      JS2.src = srcold2;
    }
  } catch (error) {
    JS1.src = 'https://localhost:8443/CLodopfuncs.js?priority=1';
    JS2.src = 'https://localhost:8444/CLodopfuncs.js?priority=0';
  }

  JS1.onload = JS2.onload = function () { CLodopJsState = "complete"; loadFinish() }
  JS1.onerror = JS2.onerror = function (evt) { CLodopJsState = "complete"; loadFinish()}
  head.insertBefore(JS1, head.firstChild);
  head.insertBefore(JS2, head.firstChild);
  CLodopIsLocal = !!((JS1.src + JS2.src).match(/\/\/localho|\/\/127.0.0./i));
}

if (needCLodop()) { loadCLodop(); }//

//==LODOP,:==
export function getLodop(oOBJECT, oEMBED) {
  let netTransport = '';
  let Clop = ''
  var is64IE = isIE && !!(ua.match(/x64/i));
  if (verChrome[0].match(/\d+/)[0] >= 84) {
    if (is64IE) {
      netTransport = 'install_lodop64'
    } else {
      netTransport = 'install_lodop32'
    }
    Clop = 'CLodop_Setup_for_Win32NT'
  } else {
    netTransport = 'CLodop_Setup_for_Win32NT_https_3.083Extend'
    Clop = 'CLodop_Setup_for_Win32NT_https_3.083Extend'
  }
  var strHtmInstall = `<br><font color='#FF00FF'>!<a href='https://gyl.taijierp.com/open/TjerpLop/DownLodTJERPLodop' target='_self'></a>,</font>`;
  var strHtmUpdate = `<br><font color='#FF00FF'>!<a href='https://gyl.taijierp.com/open/TjerpLop/DownLodTJERPLodop' target='_self'></a>,</font>`;
  var strHtm64_Install = `<br><font color='#FF00FF'>!<a href='https://gyl.taijierp.com/open/TjerpLop/DownLodTJERPLodop' target='_self'></a>,</font>`;
  var strHtm64_Update = `<br><font color='#FF00FF'>!<a href='https://gyl.taijierp.com/open/TjerpLop/DownLodTJERPLodop' target='_self'></a>,</font>`;
  var strHtmFireFox = "<br><br><font color='#FF00FF'>LodopnpActiveXPLugin,->-></font>";
  var strHtmChrome = "<br><br><font color='#FF00FF'>(</font>";
  var strCLodopInstall_1 = `<br><font color='#FF00FF'>WebCLodop<a href='https://gyl.taijierp.com/open/TjerpLop/DownLodTJERPLodop' target='_self'></a>`;
  var strCLodopInstall_2 = "<br><a href='CLodop.protocol:setup' target='_self'></a>";
  var strCLodopInstall_3 = "</font>";
  var strCLodopUpdate = `<br><font color='#FF00FF'>WebCLodop!<a href='https://gyl.taijierp.com/open/TjerpLop/DownLodTJERPLodop' target='_self'></a>,</font>`;
  var LODOP;
  try {
    var ua = navigator.userAgent;
    var isIE = !!(ua.match(/MSIE/i)) || !!(ua.match(/Trident/i));
    if (needCLodop()) {
      try {
        LODOP = getCLodop();
      } catch (err) { }
      if (!LODOP && CLodopJsState !== "complete") {
        if (CLodopJsState == "loading") {
          MessageBox({
            title: '',
            message: '',
            dangerouslyUseHTMLString: true
          })
        } else {
          MessageBox({
            title: '',
            message: 'CLodopjsloadCLodop',
            dangerouslyUseHTMLString: true
          })
        }
        return;
      }
      if (!LODOP) {
        MessageBox({
          title: '',
          message: strCLodopInstall_1 + (CLodopIsLocal ? strCLodopInstall_2 : "") + strCLodopInstall_3,
          dangerouslyUseHTMLString: true
        })
        return;
      } else {
        if (CLODOP.CVERSION < "4.1.0.1" && verChrome[0].match(/\d+/)[0] >= 84) {
          MessageBox({
            title: '',
            message: strCLodopUpdate,
            dangerouslyUseHTMLString: true
          })
        }
        if (oEMBED && oEMBED.parentNode)
          oEMBED.parentNode.removeChild(oEMBED); //
        if (oOBJECT && oOBJECT.parentNode)
          oOBJECT.parentNode.removeChild(oOBJECT);
      }
    } else {
      var is64IE = isIE && !!(ua.match(/x64/i));
      //==Lodop,:==
      if (oOBJECT || oEMBED) {
        if (isIE)
          LODOP = oOBJECT;
        else
          LODOP = oEMBED;
      } else if (!CreatedOKLodopObject) {
        LODOP = document.createElement("object");
        LODOP.setAttribute("width", 0);
        LODOP.setAttribute("height", 0);
        LODOP.setAttribute("style", "position:absolute;left:0px;top:-100px;width:0px;height:0px;");
        if (isIE)
          LODOP.setAttribute("classid", "clsid:2105C259-1E0C-4534-8141-A753534CB4CA");
        else
          LODOP.setAttribute("type", "application/x-print-lodop");
        document.documentElement.appendChild(LODOP);
        CreatedOKLodopObject = LODOP;
      } else
        LODOP = CreatedOKLodopObject;
      //==Lodop:==
      if ((!LODOP) || (!LODOP.VERSION)) {
        if (ua.indexOf('Chrome') >= 0)
          MessageBox({
            title: '',
            message: strHtmChrome,
            dangerouslyUseHTMLString: true
          })
        if (ua.indexOf('Firefox') >= 0) {
          let strHtm64 = is64IE ? strHtm64_Install : strHtmInstall
          MessageBox({
            title: '',
            message: strHtmFireFox + strHtm64,
            dangerouslyUseHTMLString: true
          })
        }
        return LODOP;
      }
    }
    if (LODOP.VERSION < "6.2.2.0" && verChrome[0].match(/\d+/)[0] >= 84) {
      if (!needCLodop()) {
        let htmls = is64IE ? strHtm64_Update : strHtmUpdate;
        MessageBox({
          title: '',
          message: htmls,
          dangerouslyUseHTMLString: true
        })
      }
    }
    //===():==
    LODOP.SET_LICENSES("", "3A6CA10946B11B73566A29C9FB3098D5", "", "")

    //=======================================================
    return LODOP;
  } catch (err) {
    alert("getLodop:" + err);
  }
}
