export var tableHead = {
  data() {
    return {
      editRules: {
        goods_name: [
          { required: true, message: '' }
        ],
        batch: [
          { required: true, message: '' }
        ],
        stocks: [
          { required: true, message: '' }
        ],
        // is_entity: [
        //   { required: true, message: '' }
        // ]
      },
      tableColumn: [
        {
          type: "checkbox",
          width: 44,
          align: 'center'
        },
        {
          title: '',
          fixed: 'right',
          width: 120,
          slots: {
            default: (scope) => {
              var a = []
              if (scope.row.is_extract != 1) {
                a = [<el-button type="text" style='margin-left: 20px;' size="small" onClick={() => { this.insert(scope.row) }}  >
                  
                </el-button>,
                <el-button type="text" size="small" onClick={() => { this.copy(scope.row) }} >
                  
                </el-button>]
              }
              return a
            }
          },
        },
        {
          title: '',
          type: "index",
          width: 60
        },
        {
          title: '',
          field: 'goods_id',
          width: 80
        },
        {
          title: '',
          field: 'goods_name',
          width: 450,
          editRender: {
            name: 'vGoods',
            getParams: (scope) => {
              return {
                params: {
                  company_id: this.params.company_id,
                  is_incomplete: 0
                },
                showObj: {
                  goods_id: "",
                  goods_name: "",
                  unit_name: "",
                  brand_name: "",
                  specification_type: "",
                  factory_code: ""
                }
              }
            },
            events: {
              change: this.handleSelect
            }
          }
        },
        {
          title: '/',
          field: 'batch',
          width: 130,
          editRender: {
            name: 'vBatch',
            getParams: ({ row }) => {
              return {
                url: 'getGoodsBatchList',
                placeholder: ',1421',
                params: {
                  company_id: this.params.company_id,
                  warehouse_id: this.params.warehouse_id,
                  goods_id: row.goods_id
                },
                type: 2
              }
            },
            events: {
              change: (val, { row }) => {
                row.batch = val.batch
                row.allocation_name = null
                row.allocation_id = null
              },
              blur: this.blurChange
            }
          },
        },
        {
          title: '',
          field: 'unit_name',
          width: 80
        },
        {
          title: '',
          field: 'allocation_name',
          width: 130,
          // editRender: { type: 'default' },
          editRender: {
            name: 'vAllocation',
            getParams: ({ row }) => {
              return {
                url: 'stockTakingGetGoodsList',
                params: Object.assign({}, {
                  ...this.params,
                  host_goods_id: row.goods_id,
                  batch: row.batch,
                  type: 2
                })
              }
            },
            events: {
              change: this.allocationChange
            }
          }
        },
        {
          title: '',
          field: 'stocks',
          headerAlign: "right", // 
          align: "right", // 
          width: 80
        },
        {
          title: '',
          field: 'real_stocks',
          headerAlign: "right", // 
          align: "right", // 
          width: 120,
          editRender: {
            name: 'number',
            events: {
              input: ({ row, column }, event) => {
                this.toPositiveNum(row, 'real_stocks')
              }
            }
          }
        },
        {
          title: '/',
          field: 'stocks_num',
          headerAlign: "right", // 
          align: "right", // 
          width: 120,
        },
        {
          title: '',
          field: 'remark',
          width: 300,
          editRender: {
            name: 'Vinput',
            formatter: 'String'
          }
        },
        {
          title: '',
          field: 'is_entity',
          editRender: {
            name: 'vSelect',
            getParams: () => {
              return {
                list: [
                  {
                    label: '',
                    value: ''
                  },
                  {
                    label: '',
                    value: ''
                  }
                ]
              }
            },
          },
          width: 120,
        },
      ]
    }
  },
  methods: {
    blurChange({ row }, that) {
      if (row.batch.toString().length != 4) {
        this.$message.warning("/1421");
        this.$set(row, "batch", '');
        that.query = ""
      } else {
        if (row.batch.toString().substr(0, 2) > 52 || row.batch.toString().substr(2, 2) > 21) {
          this.$message.warning("/1421");
          this.$set(row, "batch", '');
          that.query = ""
        }
      }
    },
    activeMethod({ column, row }) {
      if (column.property === "goods_name") {// 
        if (!this.params.warehouse_id) {
          this.$message.warning('')
          return false
        }
      }
      if (column.property === "batch") { // 
        if (!row.goods_id) {
          this.$message.warning('')
          return false
        }
      }
      if (column.property === "allocation_name") { // 
        if (!row.batch) {
          this.$message.warning('')
          return false
        }
      }
      return true
    }
  }
}
